<template>
  <footer class="footer">
    <router-link :class="['footer__link', linkActived == '/' ? 'footer__link__actived' : '']" to="/"><i class="fa fa-home"><span style="font-size: 11px;display: block"></span></i></router-link>
    <router-link :class="['footer__link', linkActived == '/user' ? 'footer__link__actived' : '']" to="/user"><i class="fa fa-user"><span style="font-size: 11px;display: block"></span></i></router-link>
    <router-link :class="['footer__link', linkActived == '/cashroute' ? 'footer__link__actived' : '']" to="/cashroute"><i class="fa fa-list"><span style="font-size: 11px;display: block"></span></i></router-link>
  </footer>
</template>

<script>
export default {
  name: "footer",
  props: [ 'linkActived' ]
}
</script>

<style lang="scss">
  .footer{
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 60px;
    background: rgb(100, 174, 255);
    /*border-top: 1px solid #dcdcdc;*/

    .footer__link{
      padding-top: 8px;
      width: 33.3%;
      display: block;
      float: left;
      height: 100%;
      line-height: 60px;
      font-size: 30px;
      text-align: center;
      color: #dadada;
    }

    .footer__link__actived{
      color: #ffffff;
    }
  .router-link-exact-active{
    color: #ffffff;
  }
}
</style>



