      <p class="user-name font32">{{userName}}</p>
      <div class="user-items">
        <div class="user-item-con">
          <p class="user-item-tittle font16"></p>
          <p class="user-item-detail font36">{{restMoney}}</p>
        </div>
        <div class="user-item-con">
          <p class="user-item-tittle font16"></p>
          <p class="user-item-detail font36">{{projectEngage}}</p>
        </div>
        <div class="user-item-con">
          <p class="user-item-tittle font16"></p>
          <p class="user-item-detail font36">{{projectInitiate}}</p>
        </div>
        <div class="user-item-con">
          <p class="user-item-tittle font16"></p>
          <p class="user-item-detail font36 more">+</p>
