      </div>
    </div>
    <p class="join-project-tittle font32"></p>
    <div class="join-project" v-on:click="cashRoute">
        <list :parent="parent"></list>
    </div>
    <!-- <footer-component :link-actived="linkActived"></footer-component> -->
  </div>
</template>

<script>
import FooterComponent from './common-components/footer'
import List from './common-components/list'

export default {
  name: 'user',
  data () {
    return {
      linkActived: '/user',
      userName:"",
      projectEngage: "",
      restMoney:"",
      projectInitiate:"",
      parent:"User"
    }
  },
  created: function () {
    var that = this;
    this.axios.get("/user").then(function (res) {
      console.log(res);
      var data = res.data;
      that.userName = data.userName;
      that.projectEngage = data.userProject.length;
      that.restMoney = data.fortune;
      that.projectInitiate = data.projectInitiate;
    }).catch(function (err) {
      if(err=="Error: Request failed with status code 404"||err=="Error: Request failed with status code 504"){
        console.log("");
        that.$router.push({name: 'Login'});
      }
      console.log("err"+err);
    })
  },
//  created: function(){
//
//    this.query();
//  },
  methods: {
    query: function(){
      // let userName
    },
    cashRoute:function(e){
      this.$router.push(`/cashroute?id=5oegb4`)
    }
  },
  components: { FooterComponent,List}
}
</script>

