<template>
  <div class="layer">
     <img class="login__img" src="../assets/logo.png" alt="logo" >
    <div class="login__container">
      <div class="login__choose">
        <div @click="logIn" :class="['login__choose__item','login__choose__item1', choose == ''? 'login__choose__item-active':'']"> </div>
        <div @click="signUp" :class="['login__choose__item','login__choose__item2', choose == ''? 'login__choose__item-active':'' ]"> </div>
      </div>
      <form class="login__form">
        <input v-model="userID" class="login__input" type="text" placeholder="ID">
        <input v-model="userPassword" class="login__input" type="password" placeholder=" ">
        <input v-if="choose === ''" v-model="userName" class="login__input" type="text" placeholder="">
        <div v-if="choose === ''" class="login__select__container">
          <span class="login__select__span"></span>
          <select v-model="userType" class="login__select">
            <option value="0"></option>
            <option value="1"></option>
          </select>
        </div>
        <div @click="submit" class="login__submit">{{choose}}</div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data: function(){
    return {
      choose : '',
      userID : '',
      userPassword : '',
      userType:"",
      userName:""
    }
  },
  methods: {
    logIn: function(){
      this.choose = '';
    },
    signUp: function(){
      this.choose = '';
    },
    submit: function(){
      var that = this;
      if(this.choose === ""){
        this.axios.post("/login",{
          userId: this.userID,
          userPassword: this.userPassword
        }).then(function(res){
          console.log(res);
          if(res.data==="success"){
            that.$cookies.set("status","alive",60*20);
            that.$router.push({name: 'User'});
          }
        })
      }else{
        this.axios.post("/signUp",{
          userId: this.userID,
          userPassword: this.userPassword,
          userType: this.userType,
          userName: this.userName,
        }).then(function(res){
          console.log(res);
        })
      }
    }
  }
}
</script>

<style lang="scss">
  .layer{
    height: 83.5vh;
  }
  .login__img{
    padding-top: 9%;
    width: 60%;
  }
  .login__container{
    width: 80%;
    height: 6.5rem;
    margin: auto;
    position: absolute;
    top: 10%;
    left: 0;
    right: 0;
    bottom: 0;

    .login__choose{
      .login__choose__item{
        font-family: "Inziu Iosevka Slab SC";
        font-weight: bold;
        float: left;
        width: calc(50% - 1px);
        height: 1.6rem;
        font-size: 0.55rem;
        line-height: 1.6rem;
        background: #fff;
        border: 1px solid #ccc;
        border-bottom: 0;

        &.login__choose__item1{
          border-radius: 10px 0 0 0;
          border-right: 0;
        }
        &.login__choose__item2{
          border-radius: 0 10px 0 0;
          border-left: 0;
        }
        &.login__choose__item-active{
          background: #7185a7;
          color: #fff;
        }
      }
    }

    .login__form{
      .login__input{
        width: calc( 100% - 2px );
        display: block;
        height: 1.6rem;
        outline: none;
        text-indent: 20px;
        font-family: "Century Gothic";
        font-size: 0.45rem;
        border: 1px solid #ccc;
        border-bottom: 0;
      }

      .login__select__container{
        height: 1.6rem;
        line-height: 1.6rem;
        background: #fff;
        width: calc( 100% - 2px);
        border: 1px solid #ccc;
        border-bottom: 0;
        font-size: 0.35rem;
        .login__select__span{
          text-align: center;
          display: block;
          float: left;
          text-indent: 20px;
          color: #666;
        }
        .login__select{
          border: none;
          width: 200px;
          margin-top: 5%;
          height: 50%;
          vertical-align: top;
          outline: none;
          font-size: 0.35rem;
        }

      }
      .login__submit{
        width: calc( 100% - 2px );
        height: 1rem;
        font-size: 0.45rem;
        letter-spacing: 0.5rem;
        text-indent: 0.5rem;
        line-height: 1rem;
        text-align: center;
        color: #fff;
        background: #7185a7;
        border: 1px solid #8c8c8c;
        border-radius: 0 0 10px 10px;
      }
      .login__submit:hover{
        background-color: #57bfff;
      }
    }
  }
</style>



