<template>
  <ul class="program-list" v-if="hasLoadData">
    <li v-for="(item,index) in items" class="program-list__li" @click="goDetail(item.projectID)">
      <ul class="program-list__detail-list">
        <li class="program-list__detail-list__li">{{item.projectName}}</li>
        <li class="program-list__detail-list__li">{{item.projectTarget}}</li>
        <li class="program-list__detail-list__li">
          <span class="program-list__detail-list__span"></span>
          <mt-progress :value="item.projectcompleted"
                       :bar-height="8" style="height: 1rem;width: 30%"></mt-progress>
          <!--echarts,canvas-->
          <!--<chart :chart-val="item.projectcompleted"></chart>-->
        </li>
      </ul>
    </li>
  </ul>
</template>

<script>
import chart from './chart'
import axios from 'axios'
import 'mint-ui'

export default {
  title: 'list',
  data: function(){
    return {
      chartVal : 70,
      items:[],
      hasLoadData: false,
      completed:[]
    }
  },
  props:[
    "parent"
  ],
  components: { chart },
  methods:{
    goDetail:function (num) {
      this.$router.push({name: 'Detail', query: {projectID: num}});
    },
    getList:function () {
      var that = this;
      var target = "";
      if(that.parent==="Home") {
        target = "/home"
      } else {
        target = "/getEngageList"
      }
      axios.get(target)
        .then(function (response) {
          console.log(response);
          if(response.data===""){
            console.log("yes");
          } else {
            console.log("not null");
          }
          let data = response.data;
          console.log(data);
          if(data!==null) {
            let len = data.length;
            if(len>9){
              len = 9;
            }
            if (that.items.length > 0) {

              for (let i = 0; i < len; i++) {
                that.items.pop();
              }
            }
            for (let i = 0; i < len; i++) {
              that.items.push(data[i]);
            }
            that.hasLoadData = true;
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  },
  mounted:function () {
    this.getList();
  },
  created: function () {
    this.hasLoadData = false;
  }
}
</script>

<style lang="scss" scoped>
  .program-list{
    width: calc( 100% - 20px );
    margin: auto;
    list-style: none;

    .program-list__li{
      height: 3rem;
      margin-bottom: 15px;
      padding: 0.5rem;
      background: url('./../../assets/11111.png') #fff no-repeat top right;
      background-size: auto 100%;
      border-radius: 10px;

      .program-list__detail-list{
        height: 3rem;

        .program-list__detail-list__li{
          list-style: none;
          font-size: 0.4rem;
          text-align: left;
          position: relative;
          text-indent: 0.5rem;
          height: 1rem;
          line-height: 1rem;

          &::before{
            content: '';
            display: inline-block;
            height: 0.3rem;
            width: 0.3rem;
            background: #cad6e3;
            border-radius: 0.15rem;
            margin: auto;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
          }

          .program-list__detail-list__span{
            float: left;
          }

          .chart{
            width: 2rem;
            height: 2rem;
            float: left;
            margin-top: -0.5rem;
          }
        }
      }
    }
  }
</style>



